.welcome__wrapper {
  background-image: url("../img/mountins.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    background-image: url("../img/mountins-tablet.jpg");
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/mountins-desktop.jpg");
  }
}

.welcome__mountains {
  display: block;
  margin: 0 auto;
}

.welcome__description {
  padding-right: 18px;
  padding-left: 18px;
}

.welcome__title {
  font-weight: 700;
  font-size: 18px;
}

.welcome__overview {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
